import React, { Component } from 'react'
import common from "@/js/common";
import '@/css/home.scss'

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pageName: "home",
            message: common.message
        }
    }
    render() {
        return (
            <div>
                <header>{this.state.pageName}</header>
                <div className="home-container flex flex-column justify-center align-center">
                    <img src="../images/vue.jpg" alt="" />
                    <p>{this.state.message}</p>
                    <p style={{color:'#666666',fontSize:'15px'}}>This project is created by vue-app.</p>
                </div>
            </div>
        )
    }
}


